import React, { FC, PropsWithChildren } from 'react';
import { colors } from '../../../utils/colors';
import { StyledFeatureBlock, FeatureIconContainer, FeatureText } from './styled';
import { Text } from '../Text';

type BenefitBlockProps = PropsWithChildren<{
  icon: React.ReactElement;
  title: string;
  text?: React.ReactElement | string;
}>;

export const FeatureBlock: FC<BenefitBlockProps> = ({ icon, text, title, children }) => {
  return (
    <StyledFeatureBlock>
      <FeatureIconContainer>{icon}</FeatureIconContainer>
      <div>
        <p>
          <Text.Regular.Bold.md>{title}</Text.Regular.Bold.md>
        </p>
        {text ? (
          <FeatureText>
            <Text.Regular.Normal.md color={colors.primary.gray500}>
              {text}
            </Text.Regular.Normal.md>
          </FeatureText>
        ) : (
          children
        )}
      </div>
    </StyledFeatureBlock>
  );
};
