import { ClipLoaderContainer, StyledHubspotFormContainer } from './styled';
import React, { FC, useContext, useEffect } from 'react';
import { AppContext } from '../Layout/Layout';
import { ClipLoader } from 'react-spinners';
import useFetchHubSpotForms from '../../../hooks/useFetchHubSpotForms';
import { colors } from '../../../utils/colors';

type Props = {
  formId: string;
  targetId: string;
};

export const HubspotFormContainer: FC<Props> = ({ formId, targetId }) => {
  const appContext = useContext(AppContext);

  useFetchHubSpotForms(
    [appContext.hubSpot.isHubSpotFormsLoaded],
    !appContext.hubSpot.isHubSpotFormsLoaded,
    appContext.hubSpot.setIsHubSpotFormsLoaded,
  );

  useEffect(() => {
    //@ts-ignore
    window.hbspt?.forms.create({
      region: 'na1',
      portalId: '21630623',
      formId: formId,
      target: '#' + targetId,
      onFormSubmit: function () {
        //@ts-ignore
        gtag_report_conversion();
      },
      onFormReady: function (form: Element) {
        const gclidField = form.querySelector('input[name="gclid_field"]');
        if (gclidField) {
          gclidField.id = 'gclid_field';
        }
      },
    });
  }, [appContext.hubSpot.isHubSpotFormsLoaded]);

  return appContext.hubSpot.isHubSpotFormsLoaded ? (
    <StyledHubspotFormContainer id={targetId}></StyledHubspotFormContainer>
  ) : (
    <ClipLoaderContainer>
      <ClipLoader color={colors.primary.lightOrange} />
    </ClipLoaderContainer>
  );
};
