import React, { PropsWithChildren } from 'react';
import { LinkElementInternal, LinkElementExternal } from './styled';
import RightArrow from '../Icons/Arrows/RightArrow';
import { colors } from '../../../utils/colors';
import shortid from 'shortid';
import RightShortArrow from '../Icons/Arrows/RightShortArrow';

export enum LinkArrowType {
  GreaterThanSign = 'GreaterThanSign',
  ShortArrow = 'ShortArrow',
}

type LinkProps = PropsWithChildren<{
  address: string;
  arrow?: boolean;
  arrowType?: LinkArrowType;
  external?: boolean;
  color?: string;
  inline?: boolean;
}>;

const CustomLink = ({
  address,
  arrow,
  children,
  external,
  color,
  inline,
  arrowType = LinkArrowType.GreaterThanSign,
}: LinkProps) => {
  const getArrowByType = () => {
    switch (arrowType) {
      case LinkArrowType.GreaterThanSign:
        return <RightArrow color={color ?? colors.primary.blue} />;
      case LinkArrowType.ShortArrow:
        return <RightShortArrow color={color ?? colors.primary.blue} />;
    }
  };

  if (external) {
    return (
      <LinkElementExternal
        href={address}
        target="_blank"
        id={shortid.generate()}
        color={color}
        rel="noreferrer"
        inline={inline}
        arrowType={arrowType}
      >
        {children}
        {arrow && getArrowByType()}
      </LinkElementExternal>
    );
  }

  return (
    <LinkElementInternal to={address} color={color} arrowType={arrowType}>
      {children}
      {arrow && getArrowByType()}
    </LinkElementInternal>
  );
};

export default CustomLink;
