import styled from "styled-components";
import { colors } from "../../../utils/colors";
import { device } from "../../../utils/layoutBreakpoints";

export const StyledFeatureBlock = styled.div`
  display: flex;
  align-items: flex-start;
  gap: 10px;
  width: 100%;
`;

export const FeatureIconContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  margin-top: 2px;
  svg,
  div {
    max-width: 20px;
  }
`;


export const FeatureText = styled.p`
  span {
    color: ${colors.primary.gray500};
  }
`;
